<div class="controle"><div class="button-reset" @click="reset">Reset</div><div class="wrap-games"><div class="group-start" v-if="states.state == 'ready'"><span class="text">???</span></div><div class="group-game" v-else><div class="type-game"><div class="icon" :class="roulette[game.type].type"></div><span class="text">{{roulette[game.type].type}}</span></div><div class="arrow"></div><div class="user"><div class="image"><img v-if="game.user" :src="require(`@/assets/images/users/${game.user}.jpeg`)"><span class="placeholder" v-else>?</span></div><span class="text">{{game.user ? users[(game.user - 1)].name : '???'}}</span></div></div></div><div class="wrap-actions" v-if="states.state == 'accept'"><div class="option" @click="accepted('accepted')">Vai</div><div class="option" @click="accepted('refused')">Não vai</div></div><div class="wrap-actions" @click="goGame" v-else><div class="group-questions" v-if="states.state == 'questions'"><div class="option" @click="answer(alternative.id)" v-for="alternative in questions[game.question - 1].alternatives">{{alternative.text}}</div></div><span class="text" v-else>{{statesTexts[this.states.state]}}</span></div></div>