<div class="game gameview" ref="gameview"><div class="header"><div class="stars"></div><div class="tag" :class="roulette[this.$route.params.id].type"><div class="icon"></div><span class="text">{{roulette[this.$route.params.id].type}}</span></div></div><div class="wrap-game"><div class="active-user"><span class="name">{{ users[currentIdUser - 1] ? users[currentIdUser - 1].name : 'fulano' }}, você?</span><choices :data="choicesUser" @update="goGame"></choices></div><div class="roulette-h"><div class="pointer" @click="start"></div><div class="list" ref="list"><div class="item" v-for="(user, index) in usersRandom" :class="index == currentUserIndex ? 'active' : '' "><img :src="require(`@/assets/images/users/${user.id}.jpeg`)"></div></div></div></div><div class="footer"></div></div>