import { createStore } from 'vuex'

export default createStore({
	state: {
		game: {},
		users: [],
		states: {},
		questions: [],
		roulette: [],
		recados: [],
		currentRoulette: null,
		currentArrRoulette: []
	},
	getters: {
		game: state => state.game,
		users: state => state.users,
		states: state => state.states,
		roulette: state => state.roulette,
		recados: state => state.recados,
		questions: state => state.questions,
		currentRoulette: state => state.currentRoulette,
		currentArrRoulette: state => state.currentArrRoulette
	},
	mutations: {
		setRoulette(state, data){
			state.roulette = data
		},
		setCurrentRoulette(state, data){
			state.currentRoulette = data;
		},
		setCurrentArrRoulette(state, data){
			state.currentArrRoulette = data;
		},
		updateCurrentArrRoulette(state, index){
			console.log(index)
			if (index > -1) {
				state.currentArrRoulette.splice(index, 1);
			}
		},
		setQuestions(state, data){
			state.questions = data;
		},
		setGame(state, data){
			state.game = data;
		},
		setRecados(state, data){
			state.recados = data;
		},
		setUsers(state, data){
			state.users = data;
		},
		setState(state, data){
			state.states = data;
		}
	},
	actions: {
	},
	modules: {
	}
})
