import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import GameView from '../views/GameView.vue'
import ChoicesView from '../views/ChoicesView.vue'
import BeberView from '../views/BeberView.vue'
import CastigoView from '../views/CastigoView.vue'
import JulgamentoView from '../views/JulgamentoView.vue'
import ControleView from '../views/Controle.vue'
import RecadoView from '../views/RecadoView.vue'

const routes = [
	{
		path: '/',
		name: 'home',
		component: HomeView
	},
	{
		path: '/game/:id',
		name: 'game',
		component: GameView
	},
    {
		path: '/choices/:id',
		name: 'choices',
		component: ChoicesView
	},
    {
		path: '/beber',
		name: 'beber',
		component: BeberView
	},
    {
		path: '/recado',
		name: 'recado',
		component: RecadoView
	},
    {
		path: '/castigo',
		name: 'castigo',
		component: CastigoView
	},
    {
		path: '/julgamento',
		name: 'julgamento',
		component: JulgamentoView
	},
    {
		path: '/controle',
		name: 'controle',
		component: ControleView
	}
]

const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes
})

export default router
