<div class="recado"><div class="description">Mande seu recado para a paola</div><div class="wrap-input"><div class="group-input"><textarea class="input" v-model="mensagem" placeholder="Diga algo fofo"></textarea><input class="input" v-model="autor" placeholder="seu nome"></div><button class="send" @click="send" :class="[mensagem.length &gt;= 4 ? '' : 'disable', loading ? 'loading' : '']">{{loading ? 'Enviando' : 'Enviar'}} recado</button></div></div>